<template>
  <div class="detail-wrap">
    <div class="detail-left">
      <div class="product-board">
        <img :src="productIcon" alt="">
        <ul>
          <router-link v-for="item in products" :to="{ params:{id:item.id} }" tag="li" active-class="active">
            {{ item.name }}
          </router-link>
        </ul>
      </div>
    </div>
    <div class="detail-right">
      <keep-alive>
        <router-view :products="products"></router-view>
      </keep-alive>
    </div>
  </div>
</template>

<script  type="text/ecmascript-6">
  export default{
    data () {
      return{
        products: [
          {
            id:1,
            name: '数据统计',
            // path: 'A',
            active: false
          },
          {
            id:2,
            name: '数据预测',
            // path: 'B',
            active: false
          },
          {
            id:3,
            name: '流量分析',
            // path: 'C',
            active: false
          },
          {
            id:4,
            name: '广告发布',
            // path: 'D',
            active: false
          }
        ],
        imgMaps:{
          // '/detail/A' : require('../assets/images/1.png'),
          // '/detail/B' : require('../assets/images/2.png'),
          // '/detail/C' : require('../assets/images/3.png'),
          // '/detail/D' : require('../assets/images/4.png')
        }
      }
    },
    computed:{
      productIcon(){
        return this.imgMaps[this.$route.path]
      }
    }
  }
</script>

<style>
  .detail-wrap {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;
    padding-top: 20px;
  }
  .detail-left {
    float: left;
    width: 200px;
    text-align: center;
  }
  .detail-left img{
    display: block;
    margin: 0 auto;
  }
  .detail-right {
    float: left;
    width: 980px;
    margin-left: 20px;
  }
  .product-board {
    background: #fff;
    padding: 20px 0;
  }
  .product-board ul {
    margin-top: 20px;
  }
  .product-board li {
    text-align: left;
    padding: 10px 15px;
    cursor: pointer;
  }
  .product-board li.active,
  .product-board li:hover {
    background: #4fc08d;
    color: #fff;
  }
  .product-board li a {
    display: block;
  }
  .sales-board {
    background: #fff;
  }
  .sales-board-form {

  }
  .sales-board-intro h2 {
    font-size: 20px;
    padding: 20px;
  }
  .sales-board-intro p {
    background: #f7fcff;
    padding: 10px 20px;
    color: #999;
    line-height: 1.8;
  }
  .sales-board-form {
    padding: 30px 20px;
    font-size: 14px;
  }
  .sales-board-line {
    clear: both;
    padding-bottom: 20px;
  }
  .sales-board-line-left {
    display: inline-block;
    width: 100px;
  }
  .sales-board-line-right {
    display: inline-block;
    width: 75%;
  }
  .sales-board-des {
    border-top: 20px solid #f0f2f5;
    padding: 15px 20px;
  }
  .sales-board-des p {
    line-height: 1.6;
  }
  .sales-board-des h2 {
    font-size: 20px;
    padding-bottom: 15px;
  }
  .sales-board-des h3 {
    font-size: 18px;
    font-weight: bold;
    padding: 20px 0 10px 0;
  }
  .sales-board-des li {
    padding: 5px 0;
  }
  .sales-board-table {
    width: 100%;
    margin-top: 20px;
  }
  .sales-board-table th {
    background: #4fc08d;
    color: #fff;
  }
  .sales-board-table td {
    border: 1px solid #f0f2f5;
    padding: 15px;
  }
</style>



